<template>
  <div class="departamento-select">
    <div 
      class="departamento-select__option"
      v-for="option in options"
      :key="option.name"
    >
      <button
        @click="selectOption($event, option)"
      >
        {{ customizeName(option.name) }} | {{ getAbbreviation(option.departmentName) }}
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Select',
  data() {
    return {
      someProp: false,
    }
  },
  props: {
    options: {
      type: Array,
      required: true
    }
  },
  methods: {
    selectOption(e, option){
      this.unselectAllOptions()
      e.target.classList.add('chosen');
      this.$emit('role-option', option)
    },
    unselectAllOptions(){
      const optionEls = this.$el.getElementsByTagName('button');
      optionEls.forEach(option => {
        option.classList.remove('chosen')
      })
    },
    customizeName(name) {
      switch(name) {
        case 'student.headman': return 'Староста'
        case 'staff.specialist': return 'Фахівець кафедри'
        case 'staff': return 'Співробітник'
        case 'staff.secretary': return 'Вчений секретар'
        case 'staff.teacher': return 'Викладач'
        case 'staff.head': return 'Завкафедри'
        case 'staff.admin': return 'Адміністратор'
        case 'student': return 'Студент'
        case 'staff.dev': return 'Розробник'
        case 'student.dev': return 'Студент-розробник'
        case 'student.subgroup_leader': return 'Голова підгрупи'
        default: return 'Невідома роль'
      }
    },
    getAbbreviation(name) {
      if (name.split(' ').length > 1) {
        return name.split(' ').map(word => {
          return word[0].toUpperCase();
        })
        .join('');
      }
      return name
    }
  }
}
</script>